import React from 'react';
import { Image, Box, Center, Link, Text, Heading, VStack, HStack } from 'native-base';

const MediaToken = (props) => {
    return (
        <Box>
            <Box borderWidth={2} borderStyle="dotted" bg="black">
                <Image
                    source={require(props.img_path)}
                    alt={props.link}
                    size="m"
                />
            </Box>
            <Link href={props.link} >
                <Text fontFamily="body" fontSize="m" color={"white"} >
                    {props.text}
                </Text>
            </Link>
        </Box>
    );
}

